import React, { useState, useEffect } from 'react';
// import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { axios } from '../../../http';
import { Table } from '../../../components';
import {
  getAllChefSaga,
  editBlockChefStatusSaga,
  resetChefList,
  editChefFeatureSaga,
} from '../../../store/actions';
import ChefCommissionModal from './ChefCommissionModal';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

// eslint-disable-next-line no-unused-vars
const Chefs = props => {
  // eslint-disable-next-line no-unused-vars
  const [status, setStatus] = useState('approved');
  const [mounted, setMounted] = useState(false);
  const [currentRecord, setCurrentRecord] = useState(null);
  const [commissionModelOpen, setCommissionModelOpen] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);

  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };

  const dispatch = useDispatch();

  const {
    chefs: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.chef);
  const { open } = useSelector(state => state.modal);

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllChefSaga({ columnName, orderBy, search, skip: 0, limit, status }),
      );
    } else {
      dispatch(
        getAllChefSaga({ columnName, orderBy, search, skip, limit, status }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('name', 'asc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  useEffect(() => {
    if (mounted) {
      getData();
    }
  }, [status]);

  const statusAction = data => {
    dispatch(editBlockChefStatusSaga({ id: data.id, isBlock: data.status }));
  };

  const statusActionChefFeature = data => {
    dispatch(editChefFeatureSaga({ id: data.id, isFeatured: data.status }));
  };

  const tableFields = [
    {
      displayName: 'BrVendorand Name',
      key: 'restaurantName',
    },
    {
      displayName: 'Full Name',
      key: 'name',
    },
    {
      displayName: 'Email',
      key: 'email',
    },
    {
      displayName: 'Phone Number',
      key: 'phone',
    },
    {
      displayName: 'Feature',
      key: 'isFeatured',
      noSort: true,
      isStatus: true,
      positiveValue: true,
      action: statusActionChefFeature,
    },
    {
      displayName: 'Vendor Rating',
      key: 'avgRating',
      className: 'active-green',
      isRating: true,
      ratingType: 'star',
      redirectLink: '/vendors/chefs/rating-detail',
    },
    {
      displayName: 'Commission(%)',
      key: 'commissionPercentage',
      isActions: true,
      actions: [
        {
          view: record => <span>{record.commissionPercentage || 0}</span>,
          action: record => {
            setCommissionModelOpen(true);
            setCurrentRecord(record);
          },
        },
      ],
    },
    {
      displayName: 'Status',
      key: 'isBlock',
      noSort: true,
      isStatus: true,
      positiveValue: false,
      action: statusAction,
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/vendors/chefs/chef-detail',
        },
      ],
    },
  ];

  const newChefTableFields = [
    {
      displayName: 'Vendor Name',
      key: 'restaurantName',
    },
    {
      displayName: 'Full Name',
      key: 'name',
    },
    {
      displayName: 'Email',
      key: 'email',
    },
    {
      displayName: 'Phone Number',
      key: 'phone',
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/vendors/chefs/chef-detail',
        },
      ],
    },
  ];

  const rejectedChefTableFields = [
    {
      displayName: 'Vendor Name',
      key: 'restaurantName',
    },
    {
      displayName: 'Full Name',
      key: 'name',
    },
    {
      displayName: 'Email',
      key: 'email',
    },
    {
      displayName: 'Phone Number',
      key: 'phone',
    },
    {
      displayName: 'Reject Reson',
      key: 'rejectedReason',
    },
    {
      displayName: 'Actions',
      noSort: true,
      isActions: true,
      actions: [
        {
          view: <i className="fa fa-eye" aria-hidden="true" />,
          action: '/vendors/chefs/chef-detail',
        },
      ],
    },
  ];

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="box-heading">
                <h2>Vendors </h2>
              </div>

              <div className="tabs-container" id="tab-new">
                <ul className="nav nav-tabs">
                  <li className="active">
                    <a
                      data-toggle="tab"
                      href="#tab-1"
                      onClick={() => {
                        setStatus('approved');
                        if (status !== 'approved') {
                          dispatch(resetChefList());
                        }
                      }}
                    >
                      Approved Vendors
                    </a>
                  </li>
                  <li className="">
                    <a
                      data-toggle="tab"
                      href="#tab-2"
                      onClick={() => {
                        setStatus('new');
                        if (status !== 'new') {
                          dispatch(resetChefList());
                        }
                      }}
                    >
                      New Vendors
                    </a>
                  </li>
                  <li className="">
                    <a
                      data-toggle="tab"
                      href="#tab-3"
                      onClick={() => {
                        setStatus('rejected');
                        if (status !== 'rejected') {
                          dispatch(resetChefList());
                        }
                      }}
                    >
                      Rejected Vendors
                    </a>
                  </li>
                </ul>
                <div className="tab-content">
                  <div id="tab-1" className="tab-pane active">
                    <div className="panel-body">
                      <div className="coman-table" id="owner-detail-btn2">
                        <Table
                          headers={tableFields}
                          tableData={allData || null}
                          pageCount={totalRecords?.count}
                          getData={getData}
                          // link={linkObj}
                          linkPropsName="_id"
                          // statusAction={statusAction}
                          setSearchFlag={searchFlagSetter}
                          errorMsg={errorMsg || 'Loading...'}
                          // pageSizeProp={pageSize}
                        />
                      </div>
                    </div>
                  </div>
                  <div id="tab-2" className="tab-pane">
                    <div className="panel-body">
                      <div className="coman-table" id="owner-detail-btn2">
                        <Table
                          headers={newChefTableFields}
                          tableData={allData || null}
                          pageCount={totalRecords?.count}
                          getData={getData}
                          // link={linkInterviewObj}
                          linkPropsName="_id"
                          // statusAction={statusAction}
                          setSearchFlag={searchFlagSetter}
                          errorMsg={errorMsg || 'Loading...'}
                          // pageSizeProp={pageSize}
                        />
                      </div>
                    </div>
                  </div>
                  <div id="tab-3" className="tab-pane">
                    <div className="panel-body">
                      <div className="coman-table" id="owner-detail-btn2">
                        <Table
                          headers={rejectedChefTableFields}
                          tableData={allData || null}
                          pageCount={totalRecords?.count}
                          getData={getData}
                          // link={linkInterviewObj}
                          linkPropsName="_id"
                          // statusAction={statusAction}
                          setSearchFlag={searchFlagSetter}
                          errorMsg={errorMsg || 'Loading...'}
                          // pageSizeProp={pageSize}
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {commissionModelOpen && (
          <ChefCommissionModal
            setModelOpen={setCommissionModelOpen}
            chefId={currentRecord._id}
            commissionPercentage={currentRecord.commissionPercentage}
            isUpdated
            tableFilter={tableFilter}
          />
        )}
        {open && <AlertMessageModal />}
      </div>
    </div>
  );
};

export default SetTokenHeader(Chefs, axios);
